  .header-content__title {
    font-size: 2rem;
  }

  .header-content__title small {
    font-size: 1.45rem;
  }

  .header-content__text {
    font-size: 1.4rem;
  }

  .header-content__wrap {
    width: calc(87.5% - 30px);
  }

  .services__item {
    width: calc(100% - 30px);
  }

  .services__item {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }

  .services__icon {
    width: 20vw;
  }

  .services__desc {
    font-size: 2rem;
    padding-left: 0;
    padding-top: 1vw;
  }
